	list-style-type: none;
	padding-left: 2em;
	font-size: 75%;
	background-image: url( 'data:image/svg+xml;base64,' );
	background-position: left center;
	background-size: 1em 1em;
	background-repeat: no-repeat;


	li {
		display: inline-block;
		margin-left: .2em;
		margin-right: .2em;
	}


	a {
		@extend %xy-center;
		width: 2em;
		height: 2em;
		background-color: $brand-success;
		border-radius: 50%;
		opacity: .5;
		transition: opacity .2s;
	}


	a:hover { opacity: 1; }


	a:before {
		@extend %background-icon;
		content: '';
		width: 1em;
		height: 1em;
		transition: opacity 200ms;
	}


	.vk { background-color: $brand-vk; }
	.facebook { background-color: $brand-facebook; }
	.twitter { background-color: $brand-twitter; }
	.linkedin { background-color: $brand-linkedin; }
	.email { background-color: $brand-email; }


	.vk:before { @extend %icon-vk; }
	.facebook:before { @extend %icon-facebook; }
	.twitter:before { @extend %icon-twitter; }
	.linkedin:before { @extend %icon-linkedin; }
	.email:before { @extend %icon-email; }


}